import router from "./router";
import Quasar from "quasar";
export default {
  name: "app",
  router,
  props: ["apiUrl"],
  data() {
    return {
      lang: "fr",
    };
  },
  methods: {
    changeLang: function() {
      Quasar.lang.set(this.lang == "fr" ? Quasar.lang.fr : Quasar.lang.en);
    },
  },
};
